'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/hooks/useAuth';
import { ArrowLeft, Bell, Clock, CheckCircle, Trash2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import axios from 'axios';

const baseURL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000/api';

interface Notification {
    id: string;
    title: string;
    message: string;
    status: string;
    is_read: boolean;
    created_at: string;
    date_raw: string;
}

export default function NotificationPage() {
    const { user } = useAuth();
    const router = useRouter();
    const [notifications, setNotifications] = useState<Notification[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        if (!user) return;

        const fetchNotifications = async () => {
            try {
                const response = await axios.get(`${baseURL}/notifications`, {
                    params: { user_id: user.id },
                    headers: { 'Accept': 'application/json' }
                });

                if (response.data.success) {
                    setNotifications(response.data.data);
                }
            } catch (error) {
                console.error("Gagal ambil notif", error);
            } finally {
                setLoading(false);
            }
        };

        fetchNotifications();
        

    }, [user]);

    const handleMarkAsRead = async (notifId: string, isRead: boolean) => {
        if (isRead) return;

        setNotifications(prev => prev.map(n => 
            n.id === notifId ? { ...n, is_read: true } : n
        ));

        try {
            await axios.post(`${baseURL}/notifications/${notifId}/read`, {}, {
                params: { user_id: user?.id },
                headers: { 'Accept': 'application/json' }
            });
        } catch (error) {
            console.error("Gagal update status read", error);
        }
    };

    const handleMarkAllRead = async () => {
        setNotifications(prev => prev.map(n => ({ ...n, is_read: true })));
        
        try {
            await axios.post(`${baseURL}/notifications/read-all`, {}, {
                params: { user_id: user?.id },
                headers: { 'Accept': 'application/json' }
            });
        } catch (error) {
            console.error("Gagal mark all", error);
        }
    }

    const handleDelete = async (e: React.MouseEvent, notifId: string) => {
        e.stopPropagation(); 

        setNotifications(prev => prev.filter(n => n.id !== notifId));

        try {
            await axios.delete(`${baseURL}/notifications/${notifId}`, {
                params: { user_id: user?.id },
                headers: { 'Accept': 'application/json' }
            });
        } catch (error) {
            console.error("Gagal hapus notifikasi", error);
        }
    };

    const handleDeleteAll = async () => {
        if (!confirm("Apakah Anda yakin ingin menghapus SEMUA notifikasi?")) return;

        setNotifications([]);

        try {
            await axios.delete(`${baseURL}/notifications`, {
                params: { user_id: user?.id },
                headers: { 'Accept': 'application/json' }
            });
        } catch (error) {
            console.error("Gagal hapus semua", error);
        }
    }

    if (!user) return null;

    return (
        <div className="min-h-screen bg-gray-50 pb-10 mt-[20px]">
            <div className="bg-white border-b border-gray-100 sticky top-0 z-10 px-4 py-4 shadow-sm">
                <div className="max-w-2xl mx-auto flex flex-col md:flex-row md:items-center justify-between gap-4">
                    <div className="flex items-center gap-3">
                        <Button 
                            variant="ghost" 
                            size="icon" 
                            onClick={() => router.back()}
                            className="text-gray-600 hover:bg-gray-100 rounded-full"
                        >
                            <ArrowLeft className="w-6 h-6" />
                        </Button>
                        <h1 className="text-xl font-bold text-gray-900">Notifikasi</h1>
                    </div>
                    
                    <div className="flex items-center gap-2 self-end md:self-auto">
                        <Button 
                            variant="ghost" 
                            size="sm"
                            onClick={handleMarkAllRead}
                            disabled={notifications.length === 0}
                            className="text-blue-600 hover:text-blue-700 hover:bg-blue-50 text-xs md:text-sm"
                        >
                            <CheckCircle className="w-4 h-4 mr-2" />
                            Tandai Baca
                        </Button>
                        
                        <Button 
                            variant="ghost" 
                            size="sm"
                            onClick={handleDeleteAll}
                            disabled={notifications.length === 0}
                            className="text-red-500 hover:text-red-700 hover:bg-red-50 text-xs md:text-sm"
                        >
                            <Trash2 className="w-4 h-4 mr-2" />
                            Hapus Semua
                        </Button>
                    </div>
                </div>
            </div>

            <div className="max-w-2xl mx-auto px-4 mt-6">
                {loading ? (
                    <div className="space-y-4">
                        {[1, 2, 3].map((i) => (
                            <div key={i} className="h-24 bg-gray-200 rounded-xl animate-pulse" />
                        ))}
                    </div>
                ) : notifications.length === 0 ? (
                    <div className="text-center py-20">
                        <div className="bg-gray-100 w-20 h-20 rounded-full flex items-center justify-center mx-auto mb-4">
                            <Bell className="w-10 h-10 text-gray-400" />
                        </div>
                        <h3 className="text-lg font-bold text-gray-900">Belum ada notifikasi</h3>
                        <p className="text-gray-500">Semua aktivitas pesanan Anda akan muncul di sini.</p>
                    </div>
                ) : (
                    <div className="space-y-3">
                        {notifications.map((notif) => (
                            <div 
                                key={notif.id}
                                onClick={() => handleMarkAsRead(notif.id, notif.is_read)}
                                className={`
                                    group relative p-4 rounded-xl border transition-all cursor-pointer hover:shadow-md
                                    ${notif.is_read 
                                        ? 'bg-white border-gray-200' 
                                        : 'bg-orange-50 border-orange-100 shadow-sm'
                                    }
                                `}
                            >
                                <div className="flex gap-4 items-start">
                                    <div className={`
                                        mt-1 min-w-[40px] h-10 rounded-full flex items-center justify-center
                                        ${notif.is_read ? 'bg-gray-100 text-gray-500' : 'bg-[#D93F21]/10 text-[#D93F21]'}
                                    `}>
                                        <Bell className="w-5 h-5" />
                                    </div>

                                    <div className="flex-1 pr-8">
                                        <div className="flex justify-between items-start mb-1">
                                            <h4 className={`text-base ${notif.is_read ? 'font-semibold text-gray-700' : 'font-bold text-gray-900'}`}>
                                                {notif.title}
                                            </h4>
                                            {!notif.is_read && (
                                                <span className="w-2.5 h-2.5 bg-[#D93F21] rounded-full mt-1.5 animate-pulse"></span>
                                            )}
                                        </div>
                                        
                                        <p className="text-sm text-gray-600 leading-relaxed mb-2">
                                            {notif.message}
                                        </p>
                                        
                                        <div className="flex items-center gap-2 text-xs text-gray-400">
                                            <Clock className="w-3.5 h-3.5" />
                                            <span>{notif.created_at}</span>
                                        </div>
                                    </div>

                                    <button 
                                        onClick={(e) => handleDelete(e, notif.id)}
                                        className="absolute top-4 right-4 p-2 text-gray-400 hover:text-red-500 hover:bg-red-50 rounded-full transition-all opacity-0 group-hover:opacity-100 focus:opacity-100"
                                        title="Hapus notifikasi ini"
                                    >
                                        <Trash2 className="w-4 h-4" />
                                    </button>
                                </div>
                            </div>
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
}